.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body, html {
  overflow-x: hidden; /* Prevent horizontal scrolling */
  margin: 0; /* Reset default body and html margin */
  padding: 0; /* Reset default body and html padding */
}

body {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.4)), url('background1.png');
  background-size: cover; /* Optional: Cover the entire container */
  background-position: center center; /* Center the background image both horizontally and vertically */
  background-attachment: fixed; /* Make the background image fixed */
  background-color: gray;
}

.app-container {
  width: 100%; /* Set width to 100% to prevent overflow */
  justify-content: center;
}
